import { environment } from '@ali/4ever-cangjie';
import { capitalize } from 'lodash-es';

const HOT_KEY_MOD = 'Mod';
const HOT_KEY_MOD_REPLACE = environment.IS_MAC ? 'âŒ˜' : 'Ctrl';
const HOT_KEY_DIVIDER = '+';

export function hotKeyToReadableString(hotKey: string) {
  const resultStr = hotKey
    .split(HOT_KEY_DIVIDER)
    .map(capitalize)
    .join(HOT_KEY_DIVIDER);
  return resultStr.replace(HOT_KEY_MOD, HOT_KEY_MOD_REPLACE);
}

export function toPx(px: number) {
  return `${px}px`;
}
